import { Component, OnInit } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import { FuseConfigService } from '@fuse/services/config.service';
import { Router } from '@angular/router';

import { locale as spanish } from './i18n/es';

@Component({
    selector   : 'first-login',
    templateUrl: './first-login.component.html',
    styleUrls  : ['./first-login.component.scss']
})
export class FirstLoginComponent implements OnInit {

    name: string;

    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private _fuseConfigService: FuseConfigService,
        private router: Router
    ) {
        this._fuseTranslationLoaderService.loadTranslations(spanish);

        this._fuseConfigService.config = {
            layout: {
                navbar: {
                    hidden: false
                },
                toolbar: {
                    hidden: false
                }
            }
        }
    }

    ngOnInit() {
        this._fuseConfigService.getConfig().subscribe(data => {
            this.name = data.user.firstname ? data.user.firstname + ' ' + data.user.lastname : data.user.email;
        })
    }
}
